
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import { mapState, mapActions } from 'vuex'

export default {
directives: {
TransferDom
},
components: {
Group,
Grid,
GridItem,
Cell,
CellFormPreview,
Drawer,
ViewBox,
XInput,
XButton,
XHeader,
XTable,
Loading,
Swiper,
Blur,
Scroller,
Actionsheet
},
methods: {
onClickMore () {
this.showMenu = true
},
changeSystem (index) {
this.$store.commit('updateIndex', { curIndex: +index })
},
logout(){
this.$router.push("/");
}
},
mounted () {

},
watch: {
curIndex(curVal, oldVal){
console.log(curVal);
},
},
computed: {
...mapState({
route: state => state.route,
curIndex: state => state.curIndex,
path: state => state.route.path,
isLoading: state => state.vux.isLoading,
direction: state => state.vux.direction
}),
isShowSwiper () {
return /setting/.test(this.path)? false : true
},
leftOptions () {
return {
showBack: this.route.path !== '/home'
}
},
rightOptions () {
return {
showMore: /setting/.test(this.path)? false : true
}
},
headerTransition () {
return this.direction === 'forward' ? 'vux-header-fade-in-right' : 'vux-header-fade-in-left'
},
},
data () {
return {
showMenu: false,
drawerVisibility: false,
url: 'https://o3e85j0cv.qnssl.com/tulips-1083572__340.jpg',
sysList: [{
title: '1'
}, {
title: '2'
}, {
title: '3'
}],
curIrriList: [{
label: '',
value: '1'
}, {
label: '',
value: '1000101'
}, {
label: '',
value: '7-21 11:00'
}],
menus: {
'0': '1',
'1': '2',
'2': '3'
},
}
}
}



